
.studio_container{
  width: 1100px;
  margin: auto;
  
}

.grid-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2.8rem 1.16rem;
  /* justify-content: space-between; */
  /* width: 90%;
  margin: auto; */
}
  
.grid-item {
    
    
    box-sizing: border-box;
}

.pre_game_img{
  width: 16.3125rem;
  height: 9.125rem;
  flex-shrink: 0;
  flex-shrink: 0;
  /* background-color: gray; */
  border-radius: 0.7rem;
  position: relative;
  cursor: pointer;
}

.overlay2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  visibility: hidden;
  border-radius: 0.7rem;
}

.overlay2.visible {
  visibility: visible;
}



.grid-item p{
  color: #333;
  text-align: center;
  font-family: "Tmoney RoundWind";
  font-size: 1.25rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.625rem;
  margin-top: 1rem;
  margin-bottom: 0;
  width: 261px;
  word-wrap: break-word;
  word-break: keep-all;
  
}

.studio_header_wrap{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.studio_choice_btn{
  width: 7.875rem;
  height: 2.5rem;
  flex-shrink: 0;
  border-radius: 0.25rem;
  background: var(--GRAY_TAB, #D9D9D9);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
}

.studio_choice_btn p{
  color: #272727;
  font-family: "Tmoney RoundWind";
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  margin:0;
  margin-left: 0.2rem;
  margin-bottom: 0.2rem;
  transition: color 0.3s ease-in-out;
}
.studio_choice_btn:hover{
  background:#333;
  
}
.studio_choice_btn:hover p{
  color: #fff;
}

.studio_choice_btn .choice_btn_mark #Vector{
  transition: all 0.3s ease-in-out;
}

.studio_choice_btn:hover .choice_btn_mark #Vector{
  fill: #fff;
}

.studio_choice_cancel_btn{
  width: 7.875rem;
  height: 2.5rem;
  flex-shrink: 0;
  border-radius: 0.25rem;
  background: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
}

.studio_choice_cancel_btn p{
  color: #fff;
  font-family: "Tmoney RoundWind";
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  margin:0;
  margin-left: 0.2rem;
  margin-bottom: 0.2rem;
  transition: color 0.3s ease-in-out;
}

.studio_choice_cancel_btn .choice_btn_mark #Vector{
  fill: #fff;
  transition: all 0.3s ease-in-out;
}

.studio_choice_cancel_btn:hover{
  background: var(--GRAY_TAB, #D9D9D9);
}

.studio_choice_cancel_btn:hover p{
  color: #272727;
}

.studio_choice_cancel_btn:hover .choice_btn_mark #Vector{
  fill: black;
}

.studio_choice_remove_btn{
  border-radius: 0.25rem;
  background: #3280FF;
  width: 7.875rem;
  height: 2.5rem;
  flex-shrink: 0;
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
  margin-right: 0.62rem;
}

.studio_choice_remove_btn p{
  color: #FFF;
  text-align: center;
  font-family: "Tmoney RoundWind";
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  margin:0;
  margin-left: 0.2rem;
  margin-bottom: 0.2rem;
}

.studio_choice_btn_wrap{
  display: flex;
}

.pre_game_img_checkbox{
  width: 2.5rem;
  height: 2.5rem;
  flex-shrink: 0;
  border-radius: 0.25rem;
  border: 3px solid #A5A5A5;
  background: #FFF;
  position: absolute;
  top: 0.63rem;
  left: 0.69rem;
  z-index: 2;
  box-sizing: border-box;
}

.pre_game_img_checkbox {
  position: relative;
  z-index: 2;
  /* 기존 스타일 */
}

.pre_game_img_checkbox.checked{
  width: 2.5rem;
  height: 2.5rem;
  flex-shrink: 0;
  border-radius: 0.25rem;
  border: 3px solid #3280FF;
  background: #3280FF;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;

}

.paging_btn_wrap{
  display: flex;
  align-items: center;
  justify-content: center;
}

.paging_btn_number_wrap{
  display: flex;
  justify-content: center;
  margin-top: 4.25rem;
}

.paging_btn{
  width: 47px;
  height: 47px;
  flex-shrink: 0;
  border: 1px solid var(--GRAY_TAB, #D9D9D9);
  background: #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-sizing: border-box;
}

.paging_btn p{
  color: #000;
  text-align: center;
  font-family: "Tmoney RoundWind";
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 0;
}

.paging_btn.active{
  background: #3280FF;
}

.paging_btn.active p{
  color: white;
}

.paging_btn_prev{
  cursor: pointer;
}

.paging_btn_next{
  cursor: pointer;
}
