
.make_header_wrap{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 16.38rem;
    
}

.make_header{
    display: flex;
    height: 7.5rem;
    width: 1100px;
    align-items: center;
    justify-content: flex-start;
}

.make_title{
    color: #333;
    font-family: Tmoney RoundWind;
    font-size: 1.875rem;
    font-style: normal;
    font-weight: 800;
    line-height: normal;
    letter-spacing: -0.09375rem;
    margin-right: 2.25rem;
}

.make_intro{
    color: rgba(51, 51, 51, 0.60);
    font-family: Tmoney RoundWind;
    font-size: 1.125rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.045rem;

}

.make_contents{
    display: flex;
    justify-content: space-between;
}

.make_contents_item{
    flex: 1;
    text-align: center;
    margin: 0.5rem;
    cursor: pointer;
}

.make_contents_bg1{
    width: 13.125rem;
    height: 10rem;
    flex-shrink: 0;
    border-radius: 1.25rem 1.25rem 0rem 0rem;
    background:#F1F4F6;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease-in-out;
    position: relative;
    
}

.make_contents_bg_wrap:hover svg{
    transform: translateY(10px);
}

.make_contents_bg1 svg{
    transition: transform 0.3s ease-in-out;
}

.make_contents_bg1 path {
    transition: fill 0.3s ease-in-out;
    
}

.make_contents_bg1:before {
    content: "";
    position: absolute;
    width: 0;
    height: 3px;
    bottom: 0;
    
    background-color: #fff;
    transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
}

.make_contents_bg2{
    width: 13.125rem;
    height: 8.125rem;
    flex-shrink: 0;
    border-radius: 0rem 0rem 1.25rem 1.25rem;
    background:#01B0AA;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.make_contents_bg_wrap:hover .make_contents_bg1 {
    background-color: #01B0AA;
}

.make_contents_bg_wrap:hover .make_contents_bg1:before {
    width: 50%;
    
}





.make_contents_bg_wrap:hover .make_contents_bg1 path {
    fill: #fff;
}

.make_contents_bg1_thumbnail{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    word-wrap: break-word;
    
    
}

.make_contents_bg1_thumbnail img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.make_contents_bg1_thumbnail_circle{
    width: 3.875rem;
    height: 3.875rem;
    flex-shrink: 0;
    border-radius: 1.875rem;
    background: #FFF;
    box-shadow: 4px 4px 4px 0px rgba(0, 0, 0, 0.10);
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 50%;
    top: 100%;
    transform: translate(-50%, -50%);
}

.make_contents_bg1_thumbnail_circle p{
    color: #000;
    text-align: center;
    font-family: Tmoney RoundWind;
    font-size: 1rem;
    font-style: normal;
    font-weight: 800;
    line-height: normal;
    letter-spacing: -0.04rem;
}

