    position: relative;
}

.make_modal_bg_upload_img{
    position: absolute;  /* 부모 요소인 make_modal_bg_upload_wrap에 맞게 위치와 크기를 조절 */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden; /* 이미지가 요소를 벗어나지 않도록 설정 */
}

.make_modal_bg_upload_img img{
    width: 100%;  /* 이미지의 너비를 부모 요소의 너비와 같게 설정 */
    height: 100%; /* 이미지의 높이를 부모 요소의 높이와 같게 설정 */
    object-fit: contain; /* 이미지가 부모 요소에 맞게 잘리거나 늘어나지 않도록 설정 */
}

