li{
    list-style: none;
  }
  

.carousel {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: auto;
   
  }
  
  .carousel__track-container {
    
    display: flex;
    justify-content: center;
   
  }
  
  /* 690px */
  
  .carousel__track {
    display: flex;
    transform:translateX(-370px);
    transition: transform 0.5s ease-in-out;
    justify-content: center;
    
    
  }
  
  .carousel__slide {
    opacity: 0.5;
    width: 100%;
    transform: scale(0.7);
    transition: filter  0.5s ease-in-out,transform  0.5s ease-in-out;
    filter: blur(10px);
    margin: 0 -120px;
    
    
  }

  .carousel__slide img{
    width: 927.984px;
    height: 522px;
  }
  
  .carousel__slide.current{
    opacity: 1;
    width: 100%;
    transform: scale(1);
    filter: blur(0px);
    margin: 0 10px;
  }

 
  
  .carousel__arrow {
    position: absolute;
    width: 30%;
    border: none;
    cursor: pointer;
    z-index: 2;
  }
  
  .carousel__arrow--left {
    left: 25px;
    transform: rotate(180deg);
    z-index: 2;
  }
  
  .carousel__arrow--right {
    right: 25px;
    z-index: 2;
  }

  .hidden{
    display: none;
  }

  .module_select p:hover{
    color: yellow;
  }

  

  