
body, .App {
  width: 100%;
  height: 100%;
  text-align: center;
}

.index_page{
  background: linear-gradient(180deg, #B85DFF 0%, #3180FF 98.33%);
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
  width: 100%;
  height: 120vh;
  /* overflow: hidden; */
  padding-top: 4.37rem;
  
}

.bg_container{
  width: 65rem;
  height: 48.75rem;
  flex-shrink: 0;
  border-radius: 0.625rem;
  background: #FFF;
  box-shadow: 20px 20px 60px 0px rgba(0, 0, 0, 0.25);
  display: flex;
  margin-bottom: 4.81rem;
}

.bg_side{
  width: 380px;
  height: 780px;
  flex-shrink: 0;
  border-radius: 10px 0px 0px 10px;
  background: #E4E4E4;
  overflow: hidden;

  font-style: normal;
  text-transform: uppercase;

}

.bg_side_content{
  width: 100%;
  margin: auto; 
  height: 100%;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

div .side_font{
  display: flex;
  justify-content: flex-start;
}
div .side_boldfont{
  text-align: left;
}

.side_font{
  color: #0A4260;
  font-family: Noto Sans KR;
  font-size: 2.5rem;
  font-weight: 300;
  line-height: 2.25rem; /* 90% */
  margin: 0px;
}

.side_boldfont{
  color: #0A4260;
  font-family: Noto Sans KR;
  font-size: 2.125rem;
  font-style: normal;
  font-weight: 900;
  line-height: 2.25rem;
  text-transform: uppercase;
  margin: 0px;

}

.bg_side_content2{
  width: 316px;
  height: 440px;
  flex-shrink: 0;
  /* background: lightgray 0% / cover no-repeat; */
  margin-left: 55px;
}

#shadowimg{
  margin-top: 40px;
  margin-left: 50px;
}



.bg_login_content{
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
 

}


.bg_login_content_logo{
  margin-top: 60px;
  width: 14.375rem;
  height: 1.875rem;
  flex-shrink: 0;
}

.bar{
  width: 5rem;
  height: 0.125rem;
  flex-shrink: 0;
  background: var(--GRAY_TAB, #D9D9D9);
  margin-top: 40px;
}

.login_font{
  margin-top: 50px;
  color: #000;
  text-align: center;
  font-family: Noto Sans KR;
  font-size: 2.25rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.bg_login_content_input{
  display: flex;
  flex-direction: column;
}

.content_label{
  width: 43.81px;
  height: 20px;
  color: #272727;

  font-family: Noto Sans KR;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  margin-top: 10px;
}

.content_input{
  width: 28.75rem;
  height: 3.5rem;
  flex-shrink: 0;
  border-radius: 6px;
  border: 1px solid #B7CEE1;
  background: #EEF7FF;
  padding-left: 23px;
  box-sizing: border-box;
  outline: none;

  margin-top: 4px;
  
}

.content_input::placeholder{
  color: #A6BDD2;
  font-family: Noto Sans KR;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.err{
  display: flex;
  justify-content: flex-start;
}

.err_msg{
