  font-family: Noto Sans KR;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;

}

.blue_btn{
  width: 28.75rem;
  height: 3.75rem;
  flex-shrink: 0;
  border-radius: 6px;
  border-style: none;
  background: #3280FF; 

  color: #FFF;
