}

.bg_login_footer{
  color: #000;
text-align: center;
font-family: "Noto Sans KR";
font-size: 0.875rem;
font-style: normal;
font-weight: 400;
line-height: 1.25rem; /* 142.857% */
text-transform: uppercase;
  
}

.bg_footer{
  color: #FFF;
  width: 95%;

  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 4.81rem;
  margin-bottom: 2.5rem;

  

  
}

.bg_footer_btn{

  
  font-family: Noto Sans KR;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;

}

.bg_footer_btn_font{
  text-decoration: none;
  color:inherit
}

.bg_footer_address{
  color: inherit;
