}


/* 여기부터 회원가입 */


.bg_signup_form{
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 28.75rem;
}


.bg_signup_header{
  width: 100%;
  display: flex;
  
}





.bg_signup_header button{
  border: none;
  outline: none;
  background-color: inherit ;
  cursor: pointer;
  flex:1
  
  
}


.bg_signup_header p{
  color: #000;
  text-align: center;
  font-family: Noto Sans KR;
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  flex:8
}

.bg_signup_header div{
  flex:1
}


.bg_signup_content{
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
}

.category{
  display: flex;
  justify-content: center;
  width: 100%;
}

.category_title{
  color: #000;
  font-family: Noto Sans KR;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;

  margin: 0;
}

.category_line{
  width: 22.875rem;
  height: 1px;
  flex-shrink: 0;
  background: #F1EAEA;
  margin-top: 5px;
  margin-left: 20px;
}

.checkbox_container{
  text-align: start;
  margin-left: 73px;
  
}

.checkbox_container .checkbox_container_content:nth-child(1){
  margin-top: 23px;
  margin-bottom: 12px;
}

.checkbox_container .checkbox_container_content:nth-child(2){
  margin-bottom: 53px;
}

.checkbox_container_content{
  display: flex;
  justify-content: space-between;
}




.checkbox_label label{
  color: #474747;
  font-family: Inter;
  font-size: 14.5px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  
}

.checkbox_label input[type=checkbox]{
  display: none;
}

.checkbox_label input[type=checkbox] + label{
  cursor: pointer; 
    padding-left: 30px; 
    background-repeat: no-repeat;
    background-image: url('./img/loginpage/checkoff.svg');
    display: inline-block;
    height: 20px;
    
}

.checkbox_label input[type=checkbox]:checked + label {
  background-image: url('./img/loginpage/checkon.svg');
}

select option[value=""][disabled] {
	display: none;
}

select{
  appearance: none;
  cursor: pointer;
}


select::-ms-expand{

  display:none;/*for IE10,11*/

}

.select-wrap{
  position:relative;
  border-radius: 0.375rem;
  border: 1px solid #B7CEE1;
  background: #EEF7FF;
  width: 13.5rem;
  height: 3.5rem;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-left: 0.25rem;
  margin-top: 0.2rem;
  cursor: pointer;

}


.arrowdown{
  position: absolute;
  top: 25%;
  right: 10px;
  cursor: pointer;
}



.email_form{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 4px;
}

.signup_btn{
  color: #FFF;
