.nav_menu {
    position: relative;
    color: #333;
    text-align: center;
    font-family: Tmoney RoundWind;
    font-size: 1rem;
    font-style: normal;
    font-weight: 800;
    line-height: normal;
    text-decoration: none;
    margin-right: 55px;
    transition: all 0.3s ease;
  }
  
  .nav_menu::before {
    content: "";
    position: absolute;
    bottom: -30%;
    width: 100%;
    height: 2px;
    background-color: rgba(51, 51, 51, 0.7); 
    transition: all 0.3s ease;
    transform: scaleX(0);
    transform-origin: center;
  }
  
  .nav_menu:hover {
    color: rgba(51, 51, 51, 0.7);  /* 호버 시 텍스트 투명도 변경 */
  }
  
  .nav_menu:hover::before {
    background-color: #333;
    width: 100%;
    transform: scaleX(1);
    transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

